import { module } from 'modujs'
import { html } from '../utils/environment'

export default class extends module {
    constructor(m) {
        super(m)

        this.events = {
            click: 'toggle'
        }
    }

    init() {
        this.url = this.el.dataset.videoUrl
    }

    toggle(e) {
        e.preventDefault();

        if (html.classList.contains('has-modal-video-open')) {
            this.call('close', null, 'ModalVideo')
        } else {
            this.call('open', { url: this.url }, 'ModalVideo')
        }
    }
}
