import { module } from 'modujs'
import { html } from '../utils/environment'

export default class extends module {
    constructor(m) {
        super(m)

        this.events = {
            click: 'toggle'
        }
    }

    init() {
        this.data = {
            src: this.el.dataset.videoSrc,
            srcMobile: this.el.dataset.videoSrcMobile || null
        }
    }

    toggle() {
        if (html.classList.contains('has-video-open')) {
            this.call('close', null, 'ModalVideo')
        } else {
            this.call('open', this.data, 'ModalVideo')
        }
    }
}
