import { module } from 'modujs';
import { stripe } from '../globals'

export default class extends module {
    constructor(m) {
        super(m);

        // Forms
        this.$paymentForm = this.$('payment-form')[0]
        this.$intentForm = this.$('intent-form')[0]
        this.$currentForm = this.$intentForm;

        // Buttons
        this.$nextBtn = this.$('next-btn')[0]
        this.$nextBtnLabel = this.$('next-btn-label')[0]
        this.$backBtn = this.$('back-btn')[0]

        // Others
        this.$inner = this.$('inner')[0]
        this.$feedback = this.$('feedback')[0]
        this.$nextBlockingEls = Array.from(this.$('next-blocking'))

        this.confirmPaymentReturnUrl = null;
        this.paymentIntentId = null;
        this.formSuccessMessage = "Generic success message"
        this.formErrorMessage = "Generic error message"

        // Recaptcha
        this.sitekey = window.app?.recaptchaPublicKey || false;
        this.useRecaptcha = this.getData('use-recaptcha') || false;
        this.badgeContainerId = 'grecaptcha-container-id';
    }

    init() {
        // Bind next / back btns
        this.onNextBind = this.onNext.bind(this)
        this.$nextBtn.addEventListener('click', this.onNextBind)
        this.onBackBind = this.onBack.bind(this)
        this.$backBtn.addEventListener('click', this.onBackBind)

        // Bind first step radio
        this.onNextBlockingChange = (e) => {
            this.enableNextBtn(e.currentTarget.checkValidity())
        }
        for(let nextBlockingEl of this.$nextBlockingEls) nextBlockingEl.addEventListener('change', this.onNextBlockingChange)

        // Bind intent submit
        this.onIntentSubmitBind = this.onIntentSubmit.bind(this)
        this.$intentForm.addEventListener('submit', this.onIntentSubmitBind)

        // Bing payment submit
        this.onPaymentSubmitBind = this.onPaymentSubmit.bind(this)
        this.$paymentForm.addEventListener('submit', this.onPaymentSubmitBind)

        // Dynamically change the styles of an element
        this.onResize = () => {
            this.elements?.update?.({
                appearance: this.getStripeAppearance()
            })
        }
        window.addEventListener('resize', this.onResize);
    }

    // ==========================================================================
    // NEXT / PREV
    // ==========================================================================

    // Buttons utils
    // ==========================================================================
    enableNextBtn(value) {
        this.$nextBtn.disabled = !value;
    }

    updateNextBtnLabel(value) {
        this.$nextBtnLabel.classList.remove('-animate');

        requestAnimationFrame(() => {
            this.$nextBtnLabel.classList.add('-animate');
            this.$nextBtnLabel.innerText = value;
            this.$currentForm.dataset.preorderNextLabel = value;
        })
    }

    enableBackBtn(value) {
        this.$backBtn.disabled = !value;
    }

    showBackBtn(value) {
        this.$backBtn.classList.toggle('is-hidden', !value);
    }

    // Navigation methods
    // ==========================================================================
    onNext() {
        if(this.$currentForm.id == 'intent-form') {
            let step = Array.from(this.$('step', this.$currentForm)).find(step => step.classList.contains('is-active'))

            if(step.nextElementSibling) {
                step.classList.remove('is-active')
                this.setIntentStep(step.nextElementSibling)
            } else {
                this.$currentForm.requestSubmit();
            }
        } else if(this.$currentForm.id == 'payment-form') {
            this.$currentForm.requestSubmit();
        }
    }

    onBack() {
        this.clearFeedback();

        if(this.$currentForm.id == 'intent-form') {
            let step = Array.from(this.$('step', this.$currentForm)).find(step => step.classList.contains('is-active'))

            if(step.previousElementSibling) {
                step.classList.remove('is-active')
                this.setIntentStep(step.previousElementSibling)
            } else {
                // ???
            }
        } else if(this.$currentForm.id == 'payment-form') {
            this.setActiveForm('intent-form');
        }
    }

    focusContainer(container) {
        let focusable = Array.from(container.querySelectorAll('button, [href], input, select, textarea, [tabindex]:not([tabindex="-1"])')).filter(el => !el.hasAttribute('disabled') && !el.getAttribute('aria-hidden') && !window.getComputedStyle(el).display != 'none')
        let firstFocusable = focusable[0];
        firstFocusable.focus();
    }

    // Allows to change current form
    setActiveForm(id) {
        const newForm = this.el.querySelector('#'+id)
        if(newForm) {
            this.$inner.scrollTop = 0;
            this.$currentForm.classList.remove('is-active');
            newForm.classList.add('is-active')
            this.focusContainer(newForm);
            this.$currentForm = newForm
            this.updateNextBtnLabel(newForm.dataset.preorderNextLabel)
        } else {
            throw 'No form matches';
        }
    }

    // Allows to change step of intent form
    setIntentStep(newStep) {
        this.$inner.scrollTop = 0;
        newStep.classList.add('is-active')
        this.focusContainer(newStep);
        const nextLabel = this.getData('next-label', newStep);
        this.updateNextBtnLabel(nextLabel)
        this.showBackBtn(newStep.previousElementSibling)
    }

    // ==========================================================================
    // FORM STATE HANDLINGS
    // ==========================================================================

    // Utils
    // ==========================================================================
    setLoading(value) {
        if(value) {
            this.clearFeedback();
            this.el.classList.add('is-loading')
            for(let fieldset of this.el.querySelectorAll('fieldset')) {
                fieldset.disabled = true;
            }
            this.enableNextBtn(false)
            this.enableBackBtn(false)
        } else {
            this.el.classList.remove('is-loading')
            for(let fieldset of this.el.querySelectorAll('fieldset')) {
                fieldset.disabled = false;
            }
            this.enableBackBtn(true)
            this.enableNextBtn(true)
        }
    }

    clearFeedback() {
        this.$feedback.classList.remove('-error')
        this.$feedback.innerHTML='';
    }

    setError(message) {
        this.$feedback.classList.add('-error')
        this.$feedback.innerText = message;
    }

    // Callbacks
    // ==========================================================================
    onIntentSubmit(e) {
        e.preventDefault();
        e.stopPropagation();

        let formObject = {}
        const formData = new FormData(e.currentTarget)
        formData.forEach((value, key) => formObject[key] = value)

        if (!formObject.phone.match(/^[\+]?[(]?[0-9]{3}[)]?[-\s\.]?[0-9]{3}[-\s\.]?[0-9]{4,6}$/im)) {
            this.setError('Invalid phone number')
            return false
        }

        if (this.useRecaptcha && this.sitekey) {

            if (!window.hasRenderedRecaptcha) {
                this.clientId = grecaptcha.render(this.badgeContainerId, {
                    'sitekey': this.sitekey,
                    'theme': 'dark',
                    'size': 'invisible',
                    'badge': 'inline',
                });
                window.hasRenderedRecaptcha = true
            }

            grecaptcha.ready(() => {
                grecaptcha.execute(this.clientId)
                    .then(token => this.processIntentForm(formObject, token));
            });
        } else {
            this.processIntentForm(formObject)
        }
    }

    processIntentForm(formObject, token = null) {

        // Disable loading state
        this.setLoading(true)

        if (this.paymentIntentId) {
            formObject.paymentIndent = this.paymentIntentId
        }

        if (token) {
            formObject['g-recaptcha-response'] = token
        }

        const formDataJSON = JSON.stringify(formObject)

        // Enable loading state
        fetch(this.$intentForm.action, {
            body: formDataJSON,
            method: this.$intentForm.method,
            headers: {
                'Content-Type': 'application/json'
            }
        // Handle HTTP response
        }).then((response) => {
            if (response.status !== 200 && response.status !== 202) {
                throw this.formErrorMessage
            }

            return response.json()
        })
        // Handle JSON data from response
        .then((response) => {
            if (!response.success || !response.client_secret) {
                throw this.formErrorMessage
            }

            if (response.payment_intent_id) {
                this.paymentIntentId = response.payment_intent_id
            }

            if (response.preorder) {
                let searchParams = new URLSearchParams(window.location.search);
                if (searchParams.has('order')) {
                    searchParams.delete('order')
                }
                searchParams.append('order', response.preorder)
                window.history.replaceState(null, null, `${window.location.pathname}?${searchParams.toString()}`)
            }

            this.setActiveForm('payment-form')

            if (response.confirmation_return_url) {
                this.confirmPaymentReturnUrl = response.confirmation_return_url;
            }

            // Give stripe the clientSecret to get the forms
            this.setupPaymentForm(response.client_secret); // Temp hardcoded secret for dev
        })
        .catch((err) => {
            // Display feedback
            this.setError(err)
        })
        .finally(() => {
            // Disable loading state
            this.setLoading(false)
        })
    }

    async onPaymentSubmit(event) {
        event.preventDefault();

        this.setLoading(true)

        const {error} = await stripe.instance.confirmPayment({
            //`Elements` instance that was used to create the Payment Element
            elements: this.elements,
            confirmParams: {
                return_url: this.confirmPaymentReturnUrl || window.location.href
            }
        });

        if (error) {
            // This point will only be reached if there is an immediate error when
            // confirming the payment. Show error to your customer (for example, payment
            // details incomplete)
            console.error(error)
            this.setError(error.message)
            this.setLoading(false)
        } else {
            // Your customer will be redirected to your `return_url`. For some payment
            // methods like iDEAL, your customer will be redirected to an intermediate
            // site first to authorize the payment, then redirected to the `return_url`.
        }
    }

    async setupPaymentForm(clientSecret) {
        await stripe.load();

        // Set up Stripe.js and Elements to use in checkout form, passing the client secret obtained in step 3
        this.elements = stripe.instance.elements({
            clientSecret,
            fonts: [
                {
                  family: 'F37Bolton',
                  src: `url(${window.location.origin}/assets/fonts/F37Bolton-Regular.woff)`,
                  weight: '400',
                }
            ],
            appearance: this.getStripeAppearance()
        });

        // Create and mount the Payment Element
        const paymentElement = this.elements.create('payment');
        paymentElement.mount('#payment-element');
        paymentElement.on('ready', () => {
            paymentElement.focus();
        })

        // Create and mount the Address Element in billing mode
        const addressElement = this.elements.create("address", {
          mode: "billing",
          allowedCountries: ['US'],
        });
        addressElement.mount("#address-element");
    }

    getStripeAppearance() {
        return {
            labels: 'floating',
            variables: {
                fontFamily: 'F37Bolton, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif',
                borderRadius: getComputedStyle(this.el).getPropertyValue('--border-radius'),
                spacingUnit: '3px',
                colorPrimary: 'black'
            },
            // rules: {
            //     '.Input': {
            //         padding: '10px'
            //     },
            //     '.Label, .Label--empty, .Label--invalid, .Label--floating, .Label--resting': {
            //         marginLeft: '10px'
            //     }
            // }
        }
    }

    // ==========================================================================
    // DESTROY
    // ==========================================================================
    destroy() {
        super.destroy()

        this.$nextBtn.removeEventListener('click', this.onNextBind)
        this.$backBtn.removeEventListener('click', this.onBackBind)

        for(let nextBlockingEl of this.$nextBlockingEls) {
            nextBlockingEl.removeEventListener('change', this.onNextBlockingChange)
        }

        this.$intentForm.removeEventListener('submit', this.onIntentSubmitBind)

        window.removeEventListener('resize', this.onResize);
    }
}

