import { module } from 'modujs';
import { html } from '../utils/environment';

export default class extends module {
    constructor(m) {
        super(m)

        this.events = {
            click: 'toggle'
        }
    }

    toggle() {
        if (html.classList.contains('has-menu-open')) {
            this.call('close', null, 'ModalMenu')
        } else {
            this.call('open', null, 'ModalMenu')
        }
    }
}
